.loginsignup{
  width: 100%;
  height: 120vh;
  background: #fce3fc;
  padding-top: 100px;
}
.loginsignup-container{
    width: 580px;
    height: 480px;
    background: white;
    margin: auto;
    padding: 40px 60px;


}

.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}

.loginsignup-fields input{
    height: 30px;
    width: 580px;
    padding-left: 20px;
    border: 1px solid white;
    outline: none;
    background: #ddd8d8;
    font-size: 18px;

}
.loginsignup-container button{
    
    width: 600px;
    height: 40px;
    color: white;
    background: #ff4141;
    cursor: pointer;
    margin-top: 30px;
    border: none;
    font-weight: 500px;

}

.loginsignup-aggre{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap: 20px;
    color: #5c5c5c;
    font-weight: 500;
    font-size: 18px;
}
.login-button span{
    color: red;
    cursor: pointer;
}