.nav-bar{
    display: flex;
    justify-content: space-around;
    padding: 15px 0px;
    box-shadow: 0 1px 3px -2px black;
    align-items: center;
}

.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}

.nav-img{
    height: 50px;
    width: auto;
}
.nav-logo p{
    color: #171717;
    font-weight: 600;
    font-size: 30px;
}

.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}

.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #ff4141;
}

.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px;
}

.nav-login-cart button{

    width: 157px;
    height: 57px;
    outline: none;
    border: 1 px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background: white;

}

.nav-login-cart button:active{
    background: #f3f3f3;
}
.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -40px;
    margin-left: -60px;
    border-radius: 11px;
    background: red;
    color: white;
}
.nav-dropdown{
    display: none;
    
}

@media(max-width:1280px){
    .nav-bar{
        padding: 12px 50px;
    }
    .nav-logo{
        width: 40px;
    }
    .nav-logo p {
        font-size: 23px;       

    }
    .nav-menu{
        gap: 30px;
        font-size: 16px;
    }
    .nav-login-cart{
        gap: 30px;
    }
    .nav-login-cart button{
      
        width: 120px;
        height: 45px;
        font-size: 16px;
    }
}

@media(max-width:1024px){
    .nav-bar{
        background-color: rgb(143, 136, 137);

        padding: 12px 30px;
    }
    
    .nav-menu{
        gap: 25px;
        font-size: 14px;
    }
   
    .nav-login-cart button{
      
        width: 80px;
        height: 30px;
        font-size: 14px;
    }
    .nav-img{
        width: 30px;
    }
    .nav-cart-count{
        font-size: 14px;
    }
}

@media(max-width:800px){
    .nav-bar{
        background-color: rgb(222, 196, 201);

        padding: 10px 0px;
    }
    .nav-dropdown{
        display: block;
        width: 35px;
        rotate: -90deg;
        transition: 0.5s;
    }
    .nav-menu{
        display: none;
        height: 80px;
        width: 100%;
        position: absolute;
        justify-content: center;
        background-color: whitesmoke;
        top: 55px;
    }

    .nav-menu-visible{
      display: flex;
    }

    .nav-logo p{
        color: #171717;
        font-weight: 600;
        font-size: 18px;
    }

    .nav-dropdown.open{
        transform: rotate(90deg);
    }
    
    
}

@media(max-width:500px){
    .nav-bar{

        background-color: rgb(231, 221, 223);
       padding:  8px 0px;
       gap: 0;
    }

    .nav-logo{
        transform: scale(0.8);
    }

    .nav-bar p{
        font-size: 14px;
    }

    .nav-menu{
        height: 70px;
        top: 50px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    }
  


    
}



