.offers{

    display: flex;
    width: 65%;
    height: 60vh;
    margin: auto;
    padding: 40px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22);
}

.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1{

    font-size: 40px;
    color: #171717;
    font-weight: 500;
    padding: 0%;
    
}

.offers-left:first-child h1{
    line-height: 0;
}

.offers-left p{

    font-size: 22px;
    color: #171717;
    font-weight: 500;
    margin-top: 20px;
}

.offers-left button{

  width: 150px;
  height: 50px;
  border-radius: 35px;
  background: #ff4141;
  border: none;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
}

.offers-right{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 50px;
}