.item{
    width: 250px;
}

.item p{
    margin: 6px,0px;
}

.item-prices{

    display: flex;
    gap: 15px;
}

.item-price-new{
    color: #374151 ;
    font-size: 18px;
    font-weight: 600;
}

.item-price-old{
    color: #8c8c8c ;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}

.item:hover{

    transform: scale(1.20);
    transition: 1s;
}