.product_display{
display: flex;
margin: 0px 170px;
}
.product_display_left{
display: flex;
gap: 20px;
}

.productdisplay_img_list{
   display: flex;
   flex-direction: column;
   gap: 16px;
}
.productdisplay_img_list img{
    height: 163px;
}

.productdisplay_main_img{
    width: 586px;
    height: 500px;
}

.product_display_right{
    display: flex;
    flex-direction: column;
    margin: 0px 170px;
}

.product_display_right button{
    padding: 15px 20px;
   height: 50px;
    width: 120px;
    font-size: 16px;
    font-weight: 500;
    color: white;
    background: #ff4141;
    margin-bottom: 40px;
    cursor: pointer;
    text-align: center;
    border-radius: 50px;
}