.cartitem{
  margin: 100px 170px;
}
.cartitem hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;
}
.cart_item_format_main{
  display: grid;
  grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
  align-items: center;
  gap: 75px;
  padding: 10px 0px;
  color: #454545;
  font-size: 18px;
  font-weight: 500;
}

.cart_item_format{
    font-size: 17px;
    font-weight: 600;

}
.cart_icon_product_icon{
    height: 62px;

}
.cartitem_removeicon{
  cursor: pointer;
}

.cartitems_down{
  display: flex;
  margin: 170px 0px;

}
.cartitems_total{
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-right: 200px;
  gap: 40px;
}
.cart_items_total_item{
  display: flex;
  justify-content: space-between;
  padding: 15px 0px;
}
.cartitems_total button{
  width: 262px;
  height: 52px;
  outline: none;
  border: none;
  background: #ff4a4a;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
}

.cart_item_promocode{
  flex: 1;
  font-size: 15px;
  font-weight: 600;

}
.cart_item_promocode p{
  color: #555;
}
.cartItem_promobox{

  width: 504px;
  margin-top: 15px;
  padding-left: 20px;
  height: 58px;
  background: #eaeaea;

}

.cartItem_promobox input{
  border: none;
  outline: none;
  background: transparent;
  font-size: 16px;
  width: 330px;
  height: 50px;
}
.cartItem_promobox button{
  width: 170px;
  height: 58px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  background: black;
  color: white;
}