.news-letter{
    width: 65%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 50px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg,#fde1ff,#e1ffea22);
    gap: 30px;
}

.news-letter h1{
   
color: #454545;
font-size: 40px;
font-weight: 600;
}

.news-letter p{
    margin: 0px;
    color: #454545;
    font-size: 20px; 
}

.news-letter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid #e3e3e3;
    box-shadow: 10px;
}

.news-letter input{

    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: poppins;
    font-size: 16px;

}

.news-letter div button{

    width: 150px;
    height: 60px;
    border-radius: 80px;
    background:black ;
    font-size:16px ;
    color: white;
    cursor: pointer;

}