.footer{
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 gap: 50px;

}

.footer-logo{
  display: flex;
  align-items: center;
  gap: 30px;

}
.footer-logo img{
    width: 100px;
}

.footer-logo p{
    color: #383838;
    font-size: 22px;
    font-weight: 500;
}

.footer-links{
   display: flex;
   list-style: none;
   gap: 20px;
   color: #252525;
   font-size: 20px;
}

.footer-links li{
    cursor: pointer;
}

.footer-social-icon{
  display: flex;
  gap: 50px;
}

.footer-icons-container{

    padding: 10px;
    padding-bottom: 6px;
    background:#fbfbfb ;
    border: 1 px solid #ebebeb;

}

.footer-copyright{

display: flex;
flex-direction: column;
align-items: center;

}